<template>
    <div class="content">
        <div class="stats">
            <div class="stats-item">
                <div class="stats-title">
                    <span>Total</span>
                    <span>Technicians</span>
                </div>
                <div class="stats-content">
                    <span class="mdi mdi-account-multiple"></span>
                    <span class="number">{{stats.totTechnicians}}</span>
                </div>
            </div>
        </div>
        <loading :active.sync="operation.inProgress"
                 :can-cancel="false"
                 :is-full-page="false"
                 :background-color="operation.loader.backgroundColor"
                 :loader="operation.loader.type"
                 :color="operation.loader.color"/>
    </div>
</template>

<script>
    import NavBar from "../components/NavBar";
    import LeftSideBar from "../components/LeftSideBar";
    import PageFooter from "../components/Footer";
    import Loading from 'vue-loading-overlay';
    import 'vue-loading-overlay/dist/vue-loading.css';

    export default {
        name: "main-dashboard",
        components: {
            LeftSideBar, NavBar, PageFooter, Loading
        },
        created() {
            // this.init()
        },
        data() {
            return {
                stats: {
                    totTechnicians: 0,
                    animateNumberSeconds: 1,
                },
                operation: {
                    inProgress: false,
                    loader: {
                        backgroundColor: '#fff',
                        color: '#001c46',
                        type: 'spinner' //Possible values: dots, spinner
                    },
                },
            }
        },
        methods: {
            getTotalTechnicians() {
                return this.$http.get(this.$fullUrl(this.$store.state.api.endPoints.getTotalTechnicians))
            },
            init() {
                let vm = this
                vm.operation.inProgress = true
                this.$http.all(
                    [
                        this.getTotalTechnicians(),
                    ]
                ).then(this.$http.spread(function (technicians) {
                    TweenLite.to(vm.stats, vm.stats.animateNumberSeconds, {
                        totTechnicians: technicians.data.total,
                        roundProps: "totTechnicians",
                        ease: Linear.easeNone
                    });
                })).catch((response) => {
                    alert("An error occurred while fetching data from server")
                }).finally(() => {
                    vm.operation.inProgress = false
                })
            },
        }
    }
</script>

