<template>
    <div class="content">
        <div class="header">
            <h2 class="md-title">Create New Technician</h2>
        </div>
        <div class="content-wrapper">
            <div class="form-wrapper">
                <form action="">
                    <div class="row">
                        <div class="col-md-6">
                            <h5 class="s-title no-bar">First name</h5>
                            <input type="text"
                                   class="s-input"
                                   v-model="f_name"
                                   placeholder="Enter first name">
                            <h5 class="s-title">Last name</h5>
                            <input type="text"
                                   class="s-input"
                                   v-model="l_name"
                                   placeholder="Enter last name">
                            <h5 class="s-title">Birth date</h5>
                            <input type="date" class="datepicker s-input" v-model="birth_date">
                            <div class="row">
                                <div class="col-md-6">
                                    <h5 class="s-title">Gender</h5>
                                    <select class="s-form-select">
                                        <option value="" selected disabled>Gender</option>
                                        <option value="Male">Male</option>
                                        <option value="Female">Female</option>
                                    </select>
                                </div>
                                <div class="col-md-6">
                                    <h5 class="s-title">Marital status</h5>
                                    <select class="s-form-select select-css">
                                        <option value="" selected disabled>Marital Status</option>
                                        <option value="Male">Single</option>
                                        <option value="Female">Married</option>
                                    </select>
                                </div>
                            </div>
                            <h5 class="s-title">Work categories(Skills)</h5>
                            <multi-select v-model="skills"
                                          :options="options.workCategories"
                                          placeholder="Select work category"
                                          label="label"
                                          track-by="value"
                                          :multiple="true"
                                          :hide-selected="true"
                                          :max-height="200"/>

                            <div id="map">
                                <gmap-map :options="map.mapOptions"
                                          :center="map.center"
                                          :zoom="map.mapZoomLevel"
                                          style="width:100%;  height: 200px;">
                                    <gmap-marker :position="map.center"></gmap-marker>
                                </gmap-map>
                            </div>
                        </div>
                        <div class="col-md-6">
                            <h5 class="s-title no-bar">E-mail</h5>
                            <input type="text"
                                   class="s-input"
                                   v-model="email"
                                   placeholder="Enter e-mail">
                            <h5 class="s-title">Phone</h5>
                            <input type="text"
                                   class="s-input"
                                   v-model="phone"
                                   placeholder="Enter phone">
                            <h5 class="s-title">Work place</h5>
                            <input type="text"
                                   class="s-input"
                                   v-model="work_place"
                                   placeholder="Work place address">
                            <div class="row">
                                <div class="col-md-6">
                                    <h5 class="s-title">Work place latitude</h5>
                                    <input type="text"
                                           class="s-input"
                                           v-model="work_place_lat"
                                           placeholder="Work place latitude">
                                </div>
                                <div class="col-md-6">
                                    <h5 class="s-title">Work place longitude</h5>
                                    <input type="text"
                                           class="s-input"
                                           v-model="work_place_long"
                                           placeholder="Work place longitude">
                                </div>
                            </div>
                            <h5 class="s-title">Profile image</h5>
                            <input type="file" id="file"
                                   ref="myFiles"
                                   class="s-input"
                                   placeholder="Select profile image"
                                   @change="previewFiles">
                            <div class="frm-actions">
                                <div class="spacer"></div>
                                <button type="button"
                                        class="btn danger min-width-104"
                                        @click="">
                                    <span class="mdi mdi-close"></span> Cancel
                                </button>
                                <button type="button" class="btn elevation-1 primary min-width-104"
                                        @click="">
                                    {{(isUpdate)? 'Update':'Save'}} <span class="mdi mdi-send"></span>
                                </button>
                            </div>
                        </div>
                    </div>

                </form>
            </div>
        </div>
        <loading :active.sync="operation.inProgress && !operation.isFetch"
                 :can-cancel="false"
                 :is-full-page="true"
                 :background-color="operation.loader.backgroundColor"
                 :loader="operation.loader.type"
                 :color="operation.loader.color"/>
    </div>
</template>

<script>
    import TInputField from "../../components/TInputField";
    import TTextField from "../../components/TTextField";
    import MultiSelect from 'vue-multiselect'
    import 'vue-multiselect/dist/vue-multiselect.min.css'
    import TSearchField from "../../components/TSearchField";
    import Loading from 'vue-loading-overlay';
    import 'vue-loading-overlay/dist/vue-loading.css';

    export default {
        name: "create-technician",
        components: {
            TInputField,
            TTextField,
            MultiSelect,
            TSearchField,
            Loading
        },
        mounted() {
            this.geoLocate()
            this.getAllWorkCategories()
        },
        data() {
            return {
                isUpdate: false,

                f_name: '',//
                l_name: '',//

                birth_date: '',//
                sex: '',//
                marital_status: '',//
                email: '',//
                phone: '',//

                work_place: '',
                work_place_lat: '',
                work_place_long: '',

                map: {
                    center: {
                        lat: -1.9546051000000002,
                        lng: 30.072289599999998,
                        name: 'Current location',
                        locationId: 0,
                    },
                    mapOptions: {
                        zoomControl: true,
                        mapTypeControl: false,
                        scaleControl: false,
                        streetViewControl: false,
                        rotateControl: false,
                        fullscreenControl: true,
                        disableDefaultUi: false
                    },
                    mapZoomLevel: 15,
                    icon: {
                        url: require('@/assets/markers/marker_5.png'),
                        scaledSize: {width: 50, height: 32},
                        labelOrigin: {x: 16, y: -10}
                    },
                },
                skills: [],
                image: null,

                options: {
                    workCategories: [],
                },

                operation: {
                    inProgress: false,
                    isFetch: false,
                    loader: {
                        backgroundColor: '#fff',
                        color: '#001c46',
                        type: 'dots' //Possible values: dots, spinner
                    },
                    response: {
                        failed: false,
                        message: '',
                        password: 'password',
                    }
                },
            }
        },
        computed: {
            markerLabel() {
                return {
                    text: this.map.center.name,
                    color: '#EB3833',
                    fontSize: "16px",
                    fontWeight: "bold"
                }
            },
            markerIcon() {
                // labelOrigin: ,
                return {
                    url: require('@/assets/markers/marker_5.png')
                }
            },
            currentLocation() {
                return navigator.geolocation
            }
        },
        methods: {
            startLoading(isFetch = false) {
                this.operation.inProgress = true
                this.operation.isFetch = isFetch
                this.operation.response.failed = false
                if (isFetch) {
                    this.messages.length = 0
                }
            },
            stopLoading() {
                this.operation.inProgress = false
            },
            geoLocate: function () {
                navigator.geolocation.getCurrentPosition(position => {
                    this.center = {
                        lat: position.coords.latitude,
                        lng: position.coords.longitude
                    };

                    this.work_place_lat = position.coords.latitude
                    this.work_place_long = position.coords.longitude
                });
            },
            getAllWorkCategories() {
                let vm = this
                vm.startLoading(true)
                this.$http.get(
                    this.$fullUrl(vm.$store.state.api.endPoints.getWorkCategories),
                ).then(function ({data}) {
                    vm.options.workCategories.length = 0
                    data.forEach((category) => {
                        vm.options.workCategories.push(
                            {label: category.category, value: category.id}
                        )
                    })
                }).catch(() => {
                    // vm.operation.response.message = "An error occurred. Try again"
                }).finally(() => {
                    vm.stopLoading()
                });
            },
            previewFiles() {
                this.image = this.$refs.myFiles.files[0];
            },
            saveTechnician() {
                let vm = this

                let data = new FormData();

                data.append('f_name', vm.f_name)
                data.append('l_name', vm.l_name)
                data.append('birth_date', vm.birth_date)
                data.append('sex', vm.sex)

                data.append('marital_status', vm.marital_status)
                data.append('email', vm.email)
                data.append('phone', vm.phone)

                data.append('work_place', vm.work_place)
                data.append('work_place_lat', vm.work_place_lat)
                data.append('work_place_long', vm.work_place_long)

                if (vm.image) {
                    data.append('image', vm.image, vm.image.name)
                } else {
                    data.append('image', null)
                }

                vm.skills.forEach((skill) => {
                    data.append('skills[]', skill.value)
                })

                vm.startLoading()

                vm.$http.post(
                    vm.$fullUrl(vm.$store.state.api.endPoints.postNewWorkCategory),
                    data
                ).then(function () {
                    alert('Technician is successfully saved')
                }).catch(() => {
                    vm.operation.response.failed = true
                    vm.operation.response.message = "An error occurred. Try again"
                }).finally(() => {
                    vm.stopLoading()
                })
            }
        }
    }
</script>

<style lang="scss">
    @import "../../assets/scss/vars";

    .s-form-select {
        /*height: 32px;*/
        border: 1px solid #ccc;
        padding: 4px 8px;
        position: relative;
        background-color: $bg-color;

        display: block;
        font-size: 16px;
        font-family: sans-serif;
        font-weight: 700;
        color: #444;
        line-height: 1.3;
        /*padding: .6em 1.4em .5em .8em;*/
        width: 100%;
        max-width: 100%;
        box-sizing: border-box;
        margin: 0;
        box-shadow: 0 1px 0 1px rgba(0, 0, 0, .04);
        border-radius: .5em;
        -moz-appearance: none;
        -webkit-appearance: none;
        appearance: none;
        outline: none;
        background-image: url('data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22292.4%22%20height%3D%22292.4%22%3E%3Cpath%20fill%3D%22%23007CB2%22%20d%3D%22M287%2069.4a17.6%2017.6%200%200%200-13-5.4H18.4c-5%200-9.3%201.8-12.9%205.4A17.6%2017.6%200%200%200%200%2082.2c0%205%201.8%209.3%205.4%2012.9l128%20127.9c3.6%203.6%207.8%205.4%2012.8%205.4s9.2-1.8%2012.8-5.4L287%2095c3.5-3.5%205.4-7.8%205.4-12.8%200-5-1.9-9.2-5.5-12.8z%22%2F%3E%3C%2Fsvg%3E'),
        linear-gradient(to bottom, #ffffff 0%, #e5e5e5 100%);
        background-repeat: no-repeat, repeat;
        background-position: right .7em top 50%, 0 0;
        background-size: .65em auto, 100%;
    }

    .select-css::-ms-expand {
        display: none;
    }

    .select-css:hover {
        border-color: #888;
    }

    .select-css:focus {
        border-color: #aaa;
        box-shadow: 0 0 1px 3px rgba(59, 153, 252, .7);
        box-shadow: 0 0 0 3px -moz-mac-focusring;
        color: #222;
        outline: none;
    }

    .s-form-select option {
        font-weight: normal;
    }

    .s-input {
        border: 1px solid #ccc;
        padding: 4px 8px;
        background-color: $bg-color;
        border-radius: 4px;
        display: block;
        width: 100%;
        margin: 4px;
    }

    .datepicker {
        border: 1px solid #ccc;
        padding: 4px 8px;
        position: relative;
        background-color: $bg-color;
        border-radius: 4px;

        &::-webkit-datetime-edit {
            /*padding: 8px 4px;*/
        }
        &::-webkit-datetime-edit-fields-wrapper {
            /*background: silver; */
        }
        &::-webkit-datetime-edit-text {
            /*color: red; padding: 4px; */
        }
        &::-webkit-datetime-edit-month-field {
            /*color: blue;*/
        }
        &::-webkit-datetime-edit-day-field {
            /*color: green; */
        }
        &::-webkit-datetime-edit-year-field {
            /*color: purple; */
        }
        &::-webkit-inner-spin-button {
            display: none;
        }
        &::-webkit-calendar-picker-indicator {
            /*background: orange; */
        }
    }

    .s-title {
        padding: 0;
        margin: 4px 0 2px !important;
        text-align: left;
        &:not(.no-bar) {
            &:before {
                content: "";
                width: 30px;
                height: 2px;
                display: block;
                background-color: black;
            }
        }
    }

    .frm-actions {
        margin-top: 24px;
    }

    #map {
        margin-top: 16px;
    }
</style>