import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import '@mdi/font/css/materialdesignicons.css'
import "@/assets/css/bootstrap-grid.min.css";
import "@/assets/css/spacing.css";
import VueCtkDateTimePicker from 'vue-ctk-date-time-picker';
import 'vue-ctk-date-time-picker/dist/vue-ctk-date-time-picker.css';
import {MdTable, MdDialog, MdContent, MdRipple} from 'vue-material/dist/components'
import 'vue-material/dist/vue-material.min.css'
import 'vue-material/dist/theme/default.css'
import MultiSelect from 'vue-multiselect'
import 'vue-multiselect/dist/vue-multiselect.min.css'
import VeeValidate from 'vee-validate';
import axios from 'axios'
import VueAxios from 'vue-axios'
import AppMixin from './mixins/AppMixin';
import VueCookies from 'vue-cookies'
import {TweenMax, Power2, TimelineLite} from "gsap/TweenMax";
import reactiveStorage from "vue-reactive-storage";
import * as VueGoogleMaps from "vue2-google-maps";

// Vue.use(TweenMax)

Vue.component('CtkDateTimePicker', VueCtkDateTimePicker);
Vue.use(require('vue-moment'))
Vue.component('MultiSelect', MultiSelect)
Vue.use(MdTable)
Vue.use(MdDialog)
Vue.use(MdContent)
Vue.use(MdRipple)
Vue.use(VeeValidate)
Vue.use(VueAxios, axios)
Vue.mixin(AppMixin)
Vue.use(VueCookies)
Vue.use(reactiveStorage, {
    "loginData": '',
})
Vue.use(VueGoogleMaps, {
    load: {
        key: "AIzaSyD5PB2PiYBu5VGyFntV40mzzxtfThpitI8",
        libraries: "places" // necessary for places input
    }
});


export const eventBus = new Vue()

// axios.defaults.headers.common['Authorization'] = store.state.api.accessToken

router.beforeEach((to, from, next) => {
    // eventBus.$emit('loading', {loading: true, reset: true});//To stop the loader on page switch
    if (to.matched.some(record => record.meta.requiresAuth) && !window.$cookies.isKey('loggedIn')) {
        next('/login');
    } else {
        next();
    }
})

Vue.config.productionTip = false

new Vue({
    router,
    store,
    render: h => h(App)
}).$mount('#app')
