<template>
    <div id="floatContainer1" class="float-container">
        <textarea :name="label"
                  :id="label"
                  :placeholder="label"
                  v-model="content"
                  data-placeholder=""
                  @input="updateValue"
                  rows="3"
                  class="vertical-scroll"></textarea>
    </div>
</template>

<script>
    export default {
        name: "t-text-field",
        props: {
            label: {
                type: String,
            },
            readonly: {
                type: Boolean,
                default: false
            },
            password: {
                type: Boolean,
                default: false
            },
            email: {
                type: Boolean,
                default: false
            },
            value: {type: String},
            icon: {
                type: String,
                default: null
            }

        },
        mounted() {
            this.init()
        },
        data() {
            return {
                content: this.value
            }
        },
        watch: {
            value(val) {
                this.content = val
            }
        },
        computed: {
            fieldType() {
                if (this.password) {
                    return 'password'
                }
                else if (this.email) {
                    return 'email'
                }
                else {
                    return 'text'
                }
            }
        },
        methods: {
            updateValue() {
                /*if(value===''){//Validation
                    this.$refs.input.value = value=''
                }*/

                this.$emit('input', this.content)
            },
            init() {
                const floatContainers = document.querySelectorAll('.float-container');

                floatContainers.forEach((element) => {
                    if (element.querySelector('textarea') && element.querySelector('textarea').value.length > 0) {
                        element.classList.add('active');
                    }

                    this.bindEvents(element);
                });
            },
            // add active class and placeholder
            handleFocus(e) {
                const target = e.target;
                target.parentNode.classList.add('active');
                target.setAttribute('placeholder', target.getAttribute('data-placeholder'));
            },
            // remove active class and placeholder
            handleBlur(e) {
                const target = e.target;
                if (!target.value) {
                    target.parentNode.classList.remove('active');
                }
                target.removeAttribute('placeholder');
            },
            // register events
            bindEvents(element) {
                const floatField = element.querySelector('input');
                if(floatField){
                    floatField.addEventListener('focus', this.handleFocus);
                    floatField.addEventListener('blur', this.handleBlur);
                }
            }
        }
    }
</script>

<style scoped lang="scss">
    @import "../assets/scss/vars";
    @import "../assets/scss/scrollbars";

    .float-container {
        border: solid 1px #ccc;
        padding: 0 8px;
        position: relative;
        background-color: $bg-color;
        margin-bottom: 4px;
        border-radius: 4px;

        textarea {
            border: none;
            font-size: 16px;
            outline: 0;
            padding: 17px 8px 4px 0;
            width: 100%;
            background-color: transparent;
            &.pd-24 {
                padding-right: 24px !important;
            }
        }
    }
</style>