<template>
    <div id="floatContainer1" class="float-container"
         :class="{'has-error': hasError}">
        <label :for="label">{{label}}</label>
        <input :type="fieldType"
               :id="label"
               :name="label"
               data-placeholder=""
               ref="input"
               v-model="content"
               @input="updateValue"
               @change="updateValue"
               @blur="$emit('blur')"
               @focus="$emit('focus')"
               :class="{'pd-24': (icon !==null)}"
               :readonly="readonly">
        <span v-show="icon"
              class="mdi mdi-24px"
              :class="icon"></span>
        <span v-if="password"
              @click="showOrHidePassword"
              class="mdi mdi-24px"
              :class="{'mdi-eye-off':isPasswordShown,'mdi-eye':!isPasswordShown}"></span>
    </div>
</template>

<script>
    export default {
        name: "t-input-field",
        $_veeValidate: {
            // must not be arrow functions.
            // the name getter
            name() {
                return this.name;
            },
            // the value getter
            value() {
                return this.value; // `this` is the component instance, `value` is the prop we added.
            }
        },
        props: {
            label: {
                type: String,
            },
            name: {
                type: String,
            },
            readonly: {
                type: Boolean,
                default: false
            },
            password: {
                type: Boolean,
                default: false
            },
            numeric: {
                type: Boolean,
                default: false
            },
            hasError: {
                type: String
            },
            email: {
                type: Boolean,
                default: false
            },
            value: {},
            icon: {
                type: String,
                default: null
            }

        },
        mounted() {
            this.init()
        },
        data() {
            return {
                content: this.value,
                isPasswordShown: false,
            }
        },
        watch: {
            value(val) {
                this.content = val
            }
        },
        computed: {
            fieldType() {
                if (this.password && !this.isPasswordShown) {
                    return 'password'
                }
                else if (this.email) {
                    return 'email'
                }
                else {
                    return 'text'
                }
            }
        },
        methods: {
            updateValue() {
                if (this.numeric && isNaN(this.content)) {//Validation
                    this.$refs.input.value = this.content = this.content.substring(0, this.content.length - 1)
                }

                this.$emit('input', this.content)
            },
            init() {
                const floatContainers = document.querySelectorAll('.float-container')

                floatContainers.forEach((element) => {
                    if (element.querySelector('input') && element.querySelector('input').value.length > 0) {
                        element.classList.add('active');
                    }

                    this.bindEvents(element);
                });
            },
            // add active class and placeholder
            handleFocus(e) {
                const target = e.target;
                target.parentNode.classList.add('active');
                target.setAttribute('placeholder', target.getAttribute('data-placeholder'));
            },
            handleInput(e) {
                const target = e.target;
                //Check the element still has .active class
                if (!(' ' + target.className + ' ').indexOf(' ' + 'active' + ' ') > -1)
                    target.parentNode.classList.add('active');
            },
            // remove active class and placeholder
            handleBlur(e) {
                const target = e.target;
                if (!target.value.length > 0) {
                    target.parentNode.classList.remove('active');
                }
                target.removeAttribute('placeholder');
            },
            // register events
            bindEvents(element) {
                const floatField = element.querySelector('input');
                if (floatField) {
                    floatField.addEventListener('focus', this.handleFocus);
                    floatField.addEventListener('input', this.handleInput);
                    floatField.addEventListener('blur', this.handleBlur);
                }
            },

            showOrHidePassword() {
                this.isPasswordShown = !this.isPasswordShown
            }
        }
    }
</script>

<style scoped lang="scss">
    @import "../assets/scss/vars";

    .float-container {
        border: solid 1px #ccc;
        padding: 0 8px;
        position: relative;
        background-color: $bg-color;
        margin-bottom: 4px;
        border-radius: 4px;
        /*display: flex;*/
        label {
            font-size: 16px;
            position: absolute;
            transform-origin: top left;
            transform: translate(0, 10px) scale(1);
            transition: all .1s ease-in-out;
        }
        input {
            border: none;
            font-size: 16px;
            outline: 0;
            padding: 17px 0 4px 0;
            width: 100%;
            background-color: transparent;
            &.pd-24 {
                padding-right: 24px !important;
            }
        }
        span {
            &.mdi {
                position: absolute;
                right: 4px;
                top: 8px;
                color: rgba(0, 0, 0, 0.5);
            }
        }
        &.active {
            label {
                transform: translate(0, 4px) scale(.70);
                color: rgba(0, 0, 0, 0.5);
            }
        }
        &.has-error {
            color: rgba(255, 0, 0, 0.8);
            border-color: rgba(255, 0, 0, 0.2);
            background-color: rgba(255, 0, 0, 0.06);
        }
    }
</style>