import Vue from 'vue'
import Router from 'vue-router'
import Login from './views/Login'
import Admin from './views/Admin'
import MainDashboard from './views/MainDashboard'
import Profile from './components/Profile'


import Technicians from './views/technicians/Technicians'
import CreateTechnician from './views/technicians/CreateTechnician'
import Map from './views/Map'
import Skills from './views/WorkCategories'
import WorkSuggestion from './views/WorkSuggestion'

Vue.use(Router)

export default new Router({
    mode: 'history',
    base: process.env.BASE_URL,
    routes: [
        {
            path: '/',
            name: 'home',
            redirect: '/login'
        },
        {
            path: '/login',
            name: 'login',
            component: Login
        },
        {
            path: '/dashboard',
            component: Admin,
            children: [
                {path: '', name: 'dashboard', component: MainDashboard, meta: {requiresAuth: true}},

                {path: 'profile', name: 'profile', component: Profile, meta: {requiresAuth: true}},


                {path: 'technicians', name: 'technicians', component: Technicians, meta: {requiresAuth: true}},
                {path: 'create-technicians', name: 'new-technician', component: CreateTechnician, meta: {requiresAuth: true}},
                {path: 'map', name: 'map', component: Map, meta: {requiresAuth: true}},
                {path: 'skills', name: 'skills', component: Skills, meta: {requiresAuth: true}},
                {path: 'work-suggestions', name: 'work-suggestions', component: WorkSuggestion, meta: {requiresAuth: true}},
            ]
        },
        //Routes for Testing


    ]
})
