<template>
    <div class="search-box">
        <div class="append-icon">
            <span class="mdi mdi-magnify"></span>
        </div>
        <input type="text"
               placeholder="Searh here..."
               v-model="content"
               @input="updateValue">
        <template v-if="prependSearchType">
            <div class="search-type" @click.stop="searchTypeShown=!searchTypeShown">
                <span class="text">{{selectedType}}</span>
                <span class="mdi"
                      :class="{'mdi-chevron-down': !searchTypeShown, 'mdi-chevron-up':searchTypeShown}"></span>
            </div>
            <div @click.stop="" v-show="searchTypeShown" class="t-dropdown-menu">
                <ul>
                    <li v-for="type in searchTypes"
                        @click="typeSelected(type)" :key="type">
                        {{type}}
                    </li>
                </ul>
            </div>
        </template>
    </div>
</template>

<script>
    export default {
        name: "t-search-field",
        props: {
            placeholder: {
                type: String,
            },
            prependSearchType: {
                type: Boolean,
                default: false,
            },
            value: {type: String},
            icon: {
                type: String,
                default: null
            },
            searchTypes: {
                type: Array
            }

        },
        mounted() {
            document.addEventListener('click', this.hideDropDown)
        },
        beforeDestroy() {
            document.removeEventListener('click', this.hideDropDown)
        },
        data() {
            return {
                content: this.value,
                searchTypeShown: false,
                selectedType: 'Search By',
            }
        },
        watch: {
            value(val) {
                this.content = val
            }
        },
        methods: {
            updateValue() {
                this.$emit('input', this.content)
            },
            typeSelected(type) {
                this.searchTypeShown = false
                this.selectedType = type
                this.$emit('type-selected', type)
            },
            hideDropDown() {
                this.searchTypeShown = false
            }
        }
    }
</script>

<style scoped lang="scss">
    @import "../assets/scss/vars";
    @import "../assets/scss/cardsBoxShadows";
    @import "../assets/scss/dropDown";
    //Search box
    .search-box {
        display: flex;
        position: relative;
        width: 100%;
        input {
            padding: 8px 8px 8px 32px;
            display: block;
            width: 100%;
            height: 32px;
            border: 1px solid $bg-color;
            &:focus {

            }
        }
        .append-icon {
            padding: 4px 16px;
            //background-color: $bg-color;
            width: 32px;
            display: flex;
            justify-content: center;
            align-items: center;
            border-top-left-radius: 4px;
            border-bottom-left-radius: 4px;
            margin-right: -32px;
            z-index: 1;
        }
        .search-type {
            display: flex;
            align-items: center;
            background-color: $bg-color;
            border-top-right-radius: 4px;
            border-bottom-right-radius: 4px;
            //margin-left: -56px;
            padding: 0 8px;
            cursor: pointer;
            .text {
                text-overflow: ellipsis;
                white-space: nowrap;
                overflow: hidden;
                display: block;
            }
        }
        /*.search-type-dropdown {
            width: 140px;
            background-color: white;
            position: absolute;
            right: 0;
            top: 32px;
            z-index: 10;
            box-shadow: $card-1-shadow;
            ul {
                list-style: none;
                padding: 8px 0;
                margin: 0;
                li {
                    cursor: pointer;
                    margin: 0;
                    padding: 4px 16px;
                    !*text-align: center;*!
                    &:hover {
                        background-color: $bg-color;
                    }
                }
            }
        }*/
        span.mdi {
            font-size: 18px;
        }
    }
</style>