<template>
    <div class="content">
        <div class="header">
            <h2 class="md-title">Technicians Range of Work Skills(Domains)</h2>
        </div>
        <div class="content-wrapper">
            <div class="row mg-y-20">
                <div class="col-md-8">
                    <div class="filter-box">
                        <t-search-field v-model="filter.searchText"/>
                    </div>
                </div>
                <div class="col-md-4">
                    <div class="flex-row">
                        <div class="spacer"></div>
                        <button class="btn elevation-1 primary" @click="openDialog(false)">
                            <span class="mdi mdi-plus mdi-18px"></span>
                            Add new category
                        </button>
                    </div>
                </div>
            </div>
            <div class="tbl-card">
                <md-table>
                    <md-table-row>
                        <md-table-head>Category</md-table-head>
                        <md-table-head>Description</md-table-head>
                        <md-table-head>Action</md-table-head>
                    </md-table-row>
                    <md-table-row v-for="(item,index) in workCategories">
                        <md-table-cell>{{ item.category }}</md-table-cell>
                        <md-table-cell>{{ item.description }}</md-table-cell>
                        <md-table-cell>
                            <div class="tbl-row-action">
                                <a href="" class="btn-link btn-update"
                                   @click.prevent="prepareOperation(item,index,true)">
                                    <span class="mdi mdi-pencil"></span> Update
                                </a>
                                <a href="" class="btn-link btn-delete"
                                   @click.prevent="prepareOperation(item,index)">
                                    <span class="mdi mdi-close"></span> Delete
                                </a>
                            </div>
                        </md-table-cell>
                    </md-table-row>
                </md-table>
                <div class="empty-table" v-show="workCategories.length === 0">
                    <small>No data available</small>
                </div>
            </div>
        </div>
        <div class="loader">
            <loading :active.sync="operation.inProgress && operation.isFetch"
                     :can-cancel="false"
                     :is-full-page="false"
                     :background-color="operation.loader.backgroundColor"
                     :loader="operation.loader.type"
                     :color="operation.loader.color"
                     :height="32"/>
        </div>


        <md-dialog :md-active.sync="dialogs.saveAndUpdate">
            <div class="frm-message vertical-scroll">
                <h2>Range of Skills for Technicians</h2>
                <div class="h-divider"></div>
                <form action="">
                    <h4 class="s-title no-bar">Category</h4>
                    <t-input-field label="Category"
                                   v-model="editedItem.category"
                                   v-validate="'required'"
                                   name="category"
                                   :has-error="errors.first('category')"/>
                    <h4 class="s-title">Description about skill</h4>
                    <t-text-field v-model="editedItem.description"
                                  label="Type here ..."
                                  name="description"
                                  v-validate="'required'"
                                  :has-error="errors.first('description')"
                                  @keyup.native.enter="saveWorkCategory"/>
                    <div class="alert-error" v-show="operation.response.failed">
                        {{operation.response.message}}
                    </div>
                    <div class="h-divider mg-y-20-force"></div>
                    <div class="frm-actions">
                        <div class="spacer"></div>
                        <button type="button" class="btn danger" @click="closeModal">
                            <span class="mdi mdi-close"></span> Cancel
                        </button>
                        <button @click="saveWorkCategory" type="button" class="btn elevation-1 primary">
                            {{(editedItem.id)? 'Update':'Save'}} <span class="mdi mdi-send"></span>
                        </button>
                    </div>
                </form>
                <loading :active.sync="operation.inProgress && !operation.isFetch"
                         :can-cancel="false"
                         :is-full-page="false"
                         :background-color="operation.loader.backgroundColor"
                         :loader="operation.loader.type"
                         :color="operation.loader.color"
                         :height="32"/>
            </div>
        </md-dialog>
        <md-dialog :md-active.sync="dialogs.deletion">
            <div class="confirmation-dialog">
                <div class="header">
                    <span class="mdi mdi-information"></span>
                </div>
                <div class="dialog-body">
                    <h2 class="dialog-title">
                        The item will be deleted permanently!
                    </h2>
                    <h5 class="dialog-sub-title">
                        Are you sure to proceed?
                    </h5>
                </div>
                <div class="dialog-actions">
                    <button class="btn-action" @click="closeModal">Cancel</button>
                    <button class="btn-action danger" @click="executeDelete">Delete</button>
                </div>
                <loading :active.sync="operation.inProgress && !operation.isFetch"
                         :can-cancel="false"
                         :is-full-page="false"
                         :background-color="operation.loader.backgroundColor"
                         :loader="operation.loader.type"
                         :color="operation.loader.color"
                         :height="32"/>
            </div>
        </md-dialog>
    </div>
</template>

<script>
    import NavBar from "../components/NavBar";
    import PageFooter from "../components/Footer";
    import LeftSideBar from "../components/LeftSideBar";

    import TInputField from "../components/TInputField";
    import TTextField from "../components/TTextField";
    import TSearchField from "../components/TSearchField";

    import Loading from 'vue-loading-overlay';
    import 'vue-loading-overlay/dist/vue-loading.css';

    export default {
        name: "work-categories",
        components: {
            TInputField,
            TTextField,
            LeftSideBar,
            NavBar,
            TSearchField,
            Loading,
            PageFooter
        },
        created() {
            this.getAllWorkCategories()
        },
        data() {
            return {
                filter: {
                    key: 'name', //name,address
                    searchText: '',
                    searchTypes: ['name', 'address']
                },
                dialogs: {
                    deletion: false,
                    saveAndUpdate: false,
                },
                editedItem: {
                    category: "",
                    description: "",
                },
                defaultItem: {
                    category: "",
                    description: "",
                },
                editedIndex: null,
                operation: {
                    inProgress: false,
                    isFetch: false,
                    loader: {
                        backgroundColor: '#fff',
                        color: '#001c46',
                        type: 'dots' //Possible values: dots, spinner
                    },
                    response: {
                        failed: false,
                        message: '',
                        password: 'password',
                    }
                },
                workCategories: [],
                saveButtonLabel: 'Save',
                isUpdate: false,
            }
        },
        computed: {},
        watch: {
            'filter.searchText': function (val, oldVal) {
                if (val.trim().length > 2) {
                    this.searchWorkCategory(val)
                }
            }
        },
        methods: {
            startLoading(isFetch = false) {
                this.operation.inProgress = true
                this.operation.isFetch = isFetch
                this.operation.response.failed = false
                if (isFetch) {
                    this.workCategories.length = 0
                }
            },
            stopLoading() {
                this.operation.inProgress = false
            },
            openDialog(isUpdate) {
                this.isUpdate = isUpdate
                this.dialogs.saveAndUpdate = true
            },
            closeModal() {
                this.dialogs.deletion = false
                this.editedItem = this.defaultItem
                this.$validator.reset()
                this.dialogs.saveAndUpdate = false
            },

            saveWorkCategory() {
                let vm = this
                this.$validator.validateAll().then(() => {
                    if (!this.errors.any()) {
                        vm.startLoading()

                        let data = {
                            category: vm.editedItem.category,
                            description: vm.editedItem.description,
                        }
                        //Update
                        if (vm.editedItem.id) {
                            vm.$http.put(
                                vm.$fullUrl(vm.$store.state.api.endPoints.putWorkCategory + vm.editedItem.id),
                                data
                            ).then(function () {
                                vm.closeModal()
                                vm.getAllWorkCategories()
                            }).catch(() => {
                                vm.operation.response.failed = true
                                vm.operation.response.message = "An error occurred. Try again"
                            }).finally(() => {
                                vm.stopLoading()
                            })
                        } else {
                            //Save
                            vm.$http.post(
                                vm.$fullUrl(vm.$store.state.api.endPoints.postNewWorkCategory),
                                data
                            ).then(function () {
                                vm.closeModal()
                                vm.getAllWorkCategories()

                            }).catch(() => {
                                vm.operation.response.failed = true
                                vm.operation.response.message = "An error occurred. Try again"
                            }).finally(() => {
                                vm.stopLoading()
                            })
                        }
                    }
                })
            },
            getAllWorkCategories() {
                let vm = this
                vm.startLoading(true)
                this.$http.get(
                    this.$fullUrl(vm.$store.state.api.endPoints.getWorkCategories),
                ).then(function ({data}) {
                    vm.workCategories = data
                }).catch(() => {
                    // vm.operation.response.message = "An error occurred. Try again"
                }).finally(() => {
                    vm.stopLoading()
                });
            },
            prepareOperation(item, index, isUpdate = false) {
                this.editedItem = item
                this.editedIndex = index
                if (isUpdate) {
                    this.openDialog()
                } else {
                    this.dialogs.deletion = true
                }
            },
            executeDelete() {
                if (this.editedItem.id) {
                    let vm = this
                    vm.startLoading()
                    vm.$http.delete(
                        vm.$fullUrl(vm.$store.state.api.endPoints.deleteWorkCategory + vm.editedItem.id)
                    ).then(function () {
                        vm.workCategories.splice(vm.editedIndex, 1)
                        vm.closeModal()
                    }).catch(() => {
                        alert('An error occurred while trying to delete')
                    }).finally(() => {
                        vm.stopLoading()
                    })
                }
            },

            searchWorkCategory(searchText) {
                let vm = this
                vm.startLoading(true)
                vm.$http.post(
                    vm.$fullUrl(vm.$store.state.api.endPoints.postSearchWorkCategory),
                    {
                        name: searchText
                    }
                ).then(function ({data}) {
                    vm.workCategories = data
                }).catch(() => {
                    alert('Not found! Try changing key text')
                }).finally(() => {
                    vm.stopLoading()
                })
            },
        }
    }
</script>

<style scoped lang="scss">
    /*@import "../assets/scss/style";*/
</style>