<script>
    import {eventBus} from '../main'

    export default {
        methods: {
            $fullUrl(uri) {
                return this.$store.state.api.baseUrl + uri
            },
            $startLoader() {
                eventBus.$emit('loading', {loading: true, reset: false})//To stop the loader on page switch
            },
            $stopLoader() {
                eventBus.$emit('loading', {loading: false, reset: true})
            },
            $notifyApp(message, isError = true, isSuccess = false, autoclose = false) {
                let error, success
                if (isError) {
                    error = true
                    success = false
                } else {
                    error = false
                    success = true
                }
                eventBus.$emit('app-error', {
                        isError: error,
                        isSuccess: success,
                        message: message,
                        autoclose: autoclose,
                    }
                )
            },
        }
    }
</script>