<template>
    <div class="content">
        <div class="header">
            <h2 class="md-title">All Technicians</h2>
        </div>
        <div class="content-wrapper">
            <div class="row mg-y-20">
                <div class="col-md-9">
                    <t-search-field
                            v-model="filter.searchText"/>
                </div>
                <div class="col-md-3">
                    <div class="flex-row">
                        <div class="spacer"></div>
                        <button class="btn elevation-1 primary" @click="navigateTo('new-technician')">
                            <span class="mdi mdi-message-plus mdi-18px"></span>
                            Add new Technician
                        </button>
                    </div>
                </div>
            </div>
            <div class="tbl-card">
                <md-table>
                    <md-table-row>
                        <md-table-head>First Name</md-table-head>
                        <md-table-head>Last Name</md-table-head>
                        <md-table-head>Birth date</md-table-head>
                        <md-table-head>E-mail</md-table-head>
                        <md-table-head>Phone</md-table-head>
                        <md-table-head>Sex</md-table-head>
                        <md-table-head>Work Place</md-table-head>
                        <md-table-head>Latitude</md-table-head>
                        <md-table-head>Longitude</md-table-head>
                        <md-table-head>Action</md-table-head>
                    </md-table-row>

                    <md-table-row v-for="(technician,index) in technicians">
                        <md-table-cell>{{technician.f_name}}</md-table-cell>
                        <md-table-cell>{{technician.l_name}}</md-table-cell>
                        <md-table-cell>{{technician.birth_date}}</md-table-cell>
                        <md-table-cell>{{technician.email}}</md-table-cell>
                        <md-table-cell>{{technician.phone}}</md-table-cell>
                        <md-table-cell>{{technician.sex}}</md-table-cell>
                        <md-table-cell>{{technician.work_place}}</md-table-cell>
                        <md-table-cell>{{technician.work_place_lat}}</md-table-cell>
                        <md-table-cell>{{technician.work_place_long}}</md-table-cell>
                        <md-table-cell>
                            <div class="tbl-row-action">
                                <a href="" class="btn-link btn-update"
                                   @click.prevent="prepareOperation(item,index,true)">
                                    <span class="mdi mdi-pencil"></span> Update
                                </a>
                                <a href="" class="btn-link btn-delete"
                                   @click.prevent="prepareOperation(item,index)">
                                    <span class="mdi mdi-close"></span> Delete
                                </a>
                            </div>
                        </md-table-cell>
                    </md-table-row>
                </md-table>
                <div class="empty-table" v-show="technicians.length === 0">
                    <small>No data available</small>
                </div>
            </div>
        </div>
        <div class="loader">
            <loading :active.sync="operation.inProgress && operation.isFetch"
                     :can-cancel="false"
                     :is-full-page="false"
                     :background-color="operation.loader.backgroundColor"
                     :loader="operation.loader.type"
                     :color="operation.loader.color"
                     :height="32"/>
        </div>

        <md-dialog :md-active.sync="dialogs.saveAndUpdate">
            <div class="frm-message vertical-scroll">
                <h2>New Call Message</h2>
                <form action="">
                    <h4 class="s-title no-bar">Message To:</h4>
                    <multi-select v-model="editedItem.selectedStaffMember"
                                  :options="options.staffMembers"
                                  placeholder="Select staff person"
                                  label="label"
                                  track-by="value"
                                  :hide-selected="true"
                                  :max-height="200">
                        <template slot="afterList" slot-scope="props">
                            <div class="multiselect-options-footer" @click.prevent="navigate('staff')">
                                <span class="footer-label">Click to add new staff member</span>
                                <span class="mdi mdi-account-plus"></span>
                            </div>
                        </template>
                    </multi-select>
                    <h4 class="s-title no-bar">Type:</h4>
                    <multi-select v-model="editedItem.selectedType"
                                  :options="options.messageTypes"
                                  placeholder="Select message type"
                                  label="label"
                                  track-by="value"
                                  :max-height="200">
                    </multi-select>
                    <h4 class="s-title">Date & Time</h4>
                    <ctk-date-time-picker
                            :id="dateTimePicker.options.id"
                            v-model="editedItem.visitedDate"
                            :format="dateTimePicker.options.format"
                            :color="dateTimePicker.options.color"
                            :button-color="dateTimePicker.options.buttonColor"
                            :no-header="dateTimePicker.options.noHeader"
                            :label="dateTimePicker.options.label"
                            :max-date="dateTimePicker.options.maxDate"
                            :no-button="dateTimePicker.options.noButton"
                            :no-button-now="dateTimePicker.options.noButtonNow"
                            :no-value-to-custom-elem="dateTimePicker.options.noValueToCustomElem">
                        <t-input-field ref="input"
                                       v-model="editedItem.visitedDate"
                                       label="Select date & time"
                                       icon="mdi-calendar"/>
                    </ctk-date-time-picker>
                    <h4 class="s-title">Person(s) that visited</h4>
                    <multi-select v-model="editedItem.selectedVisitors"
                                  :options="options.visitors"
                                  placeholder="Select at least one"
                                  label="label"
                                  track-by="value"
                                  :multiple="true"
                                  :hide-selected="true"
                                  :max-height="150"
                                  :showNoResults="false"
                                  @input="">
                        <template slot="afterList" slot-scope="props">
                            <div class="multiselect-options-footer" @click.prevent="navigate('visitors')">
                                <span class="footer-label">Click to add new visitor</span>
                                <span class="mdi mdi-account-plus"></span>
                            </div>
                        </template>
                    </multi-select>
                    <h4 class="s-title">Message Content</h4>
                    <t-text-field label="Type here ..."
                                  v-model="editedItem.message"
                                  @keyup.native.enter=""/>
                    <div class="frm-actions">
                        <div class="spacer"></div>
                        <button type="button"
                                class="btn danger min-width-104"
                                @click="closeModal">
                            <span class="mdi mdi-close"></span> Cancel
                        </button>
                        <button type="button" class="btn elevation-1 primary min-width-104"
                                @click="">
                            {{(editedItem.id)? 'Update':'Save'}} <span class="mdi mdi-send"></span>
                        </button>
                    </div>
                </form>
                <loading :active.sync="operation.inProgress && !operation.isFetch"
                         :can-cancel="false"
                         :is-full-page="false"
                         :background-color="operation.loader.backgroundColor"
                         :loader="operation.loader.type"
                         :color="operation.loader.color"
                         :height="32"/>
            </div>
        </md-dialog>
        <md-dialog :md-active.sync="dialogs.deletion">
            <div class="confirmation-dialog">
                <div class="header">
                    <span class="mdi mdi-information"></span>
                </div>
                <div class="dialog-body">
                    <h2 class="dialog-title">
                        The user will be deleted permanently!
                    </h2>
                    <h5 class="dialog-sub-title">
                        Are you sure to proceed?
                    </h5>
                </div>
                <div class="dialog-actions">
                    <button class="btn-action" @click="dialogs.deletion=false">Cancel</button>
                    <button class="btn-action danger" @click="executeDelete">Delete</button>
                </div>
                <loading :active.sync="operation.inProgress && !operation.isFetch"
                         :can-cancel="false"
                         :is-full-page="false"
                         :background-color="operation.loader.backgroundColor"
                         :loader="operation.loader.type"
                         :color="operation.loader.color"
                         :height="32"/>
            </div>
        </md-dialog>
    </div>
</template>

<script>
    import TInputField from "../../components/TInputField";
    import TTextField from "../../components/TTextField";
    import MultiSelect from 'vue-multiselect'
    import 'vue-multiselect/dist/vue-multiselect.min.css'
    import TSearchField from "../../components/TSearchField";
    import Loading from 'vue-loading-overlay';
    import 'vue-loading-overlay/dist/vue-loading.css';

    export default {
        name: "technicians",
        components: {
            TInputField,
            TTextField,
            MultiSelect,
            TSearchField,
            Loading
        },
        mounted() {
            this.getAllTechnicians()
        },
        data() {
            return {
                technicians: [],
                filter: {
                    searchText: '',
                    key: 'staff',
                    searchTypes: ['staff', 'message', 'type'], //staff, message, type
                    date: {
                        value: {
                            start: '',
                            end: ''
                        },
                        options: {
                            color: '#214272',
                            range: true,
                            formatted: 'll',
                            format: 'YYYY-MM-DD',
                            label: 'Select date range',
                            id: 'RangeDatePicker',
                            noValueToCustomElem: true,
                        }
                    }
                },
                selectedDate: null,
                dialogs: {
                    deletion: false,
                    saveAndUpdate: false,
                },
                messages: [],
                editedItem: {
                    f_name: '',
                    l_name: '',
                    birth_date: '',
                    sex: '',
                    marital_status: '',
                    email: '',
                    phone: '',
                    province: '',
                    district: '',
                    sector: '',
                    cell: '',
                    village: '',
                    work_place: '',
                    work_place_lat: '',
                    work_place_long: '',
                },
                defaultItem: {
                    f_name: '',
                    l_name: '',
                    birth_date: '',
                    sex: '',
                    marital_status: '',
                    email: '',
                    phone: '',
                    province: '',
                    district: '',
                    sector: '',
                    cell: '',
                    village: '',
                    work_place: '',
                    work_place_lat: '',
                    work_place_long: '',
                },
                options: {
                    staffMembers: [],
                    visitors: [],
                    messageTypes: [
                        {label: 'Call Message', value: 'Call Message'},
                        {label: 'Visitor Message', value: 'Visitor Message'},
                    ]
                },
                dateTimePicker: {
                    value: '2018-04-07 20:26',
                    options: {
                        format: 'YYYY-MM-DD HH:mm',
                        id: 'DateTimePicker',
                        minDate: '2018-04-05',
                        maxDate: '2018-04-24',
                        noValueToCustomElem: true,
                    }
                },
                editedIndex: -1,
                operation: {
                    inProgress: true,
                    isFetch: false,
                    loader: {
                        backgroundColor: '#fff',
                        color: '#001c46',
                        type: 'dots' //Possible values: dots, spinner
                    },
                    response: {
                        failed: false,
                        message: '',
                        password: 'password',
                    }
                },
            }
        },
        watch: {
            'filter.searchText': function (val, oldVal) {
                if (val.trim().length > 2) {
                    this.searchTechnician(val)
                }
                if (val.trim().length === 0) {
                    this.getAllTechnicians()
                }

            }
        },
        methods: {
            deleteTechnician(technician, index) {
                this.showLoader()
                axios.delete('/api/technicians/' + technician.id).then(() => {
                    this.technicians.splice(index, 1)
                }).catch((error) => {
                    console.log('Error occurred', error.response)
                    alert('An error occurred while deleting the technician. Try again later')
                }).finally(() => {
                    this.hideLoader()
                })
            },
            navigateTo(routeName) {
                this.$router.push({name: routeName})
            },
            searchTechnician(searchText) {
                let vm = this
                vm.startLoading(true)
                vm.$http.post(
                    vm.$fullUrl(vm.$store.state.api.endPoints.postSearchTechnician),
                    {
                        query: searchText
                    }
                ).then(function ({data}) {
                    vm.technicians = data
                }).catch(() => {
                    alert('Not found! Try changing key text')
                }).finally(() => {
                    vm.stopLoading()
                })
            },
            closeModal() {
                this.dialogs.deletion = false
                this.editedItem = this.defaultItem
                this.editedItem.visitedDate = this.$moment().format('YYYY-MM-DD HH:mm')
                this.$validator.reset()
                this.dialogs.saveAndUpdate = false
            },
            startLoading(isFetch = false) {
                this.operation.inProgress = true
                this.operation.isFetch = isFetch
                this.operation.response.failed = false
                if (isFetch) {
                    this.messages.length = 0
                }
            },
            stopLoading() {
                this.operation.inProgress = false
            },
            navigate(routeName) {
                this.$router.push({name: routeName, query: {new: 'true'}})
            },
            prepareOperation(item, index, isUpdate = false) {
                this.editedItem = item
                this.editedIndex = index
                if (isUpdate) {
                    this.dialogs.saveAndUpdate = true

                } else {
                    this.dialogs.deletion = true
                }
            },
            executeDelete() {
                if (this.editedItem.id) {
                    let vm = this
                    vm.startLoading()
                    vm.$http.delete(
                        vm.$fullUrl(vm.$store.state.api.endPoints.deleteTechnician + vm.editedItem.id)
                    ).then(function () {
                        vm.technicians.splice(vm.editedIndex, 1)
                        vm.closeModal()
                    }).catch(() => {
                        alert('An error occurred while trying to delete')
                    }).finally(() => {
                        vm.stopLoading()
                    })
                }
            },

            getAllTechnicians() {
                let vm = this
                vm.startLoading(true)
                vm.$http.get(
                    vm.$fullUrl(vm.$store.state.api.endPoints.getTechnicians)
                ).then(({data}) => {
                    vm.technicians = data
                }).catch((error) => {
                    alert('An error occurred. Try again')
                }).finally(() => {
                    vm.stopLoading()
                })
            }
        }
    }
</script>

<style scoped lang="scss">

</style>