<template>
    <div class="nav">
        <div class="logo-holder">
            <div>
                TechniciansFinder
            </div>
        </div>
        <div @click.stop="shown=!shown" class="user-account">
            <span class="user-name">{{localStorage.loginData.user.f_name+" "+localStorage.loginData.user.l_name}}</span>
            <span class="mdi mg-r-8"
                  :class="{'mdi-chevron-down': !shown, 'mdi-chevron-up':shown}"></span>
            <div class="profile">
                <img src="@/assets/user.jpg" alt="Profile Image">
            </div>
            <div @click.stop="shown=!shown" v-show="shown" class="t-dropdown-menu">
                <ul>
                    <li @click="logout">
                        <a @click.prevent="">
                            <span class="mdi mdi-logout"></span> Log out
                        </a>
                    </li>
                    <router-link tag="li" :to="{name: 'profile'}">
                        <a>
                            <span class="mdi mdi-settings"></span> Settings
                        </a>
                    </router-link>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "nav-bar",
        mounted() {
            document.addEventListener('click', this.hideDropDown)
        },
        beforeDestroy() {
            document.removeEventListener('click', this.hideDropDown)
        },
        data() {
            return {
                shown: false,
            }
        },
        methods: {
            hideDropDown() {
                this.shown = false
            },
            logout() {
                if (this.$cookies.isKey('loggedIn')) {
                    this.$cookies.remove('loggedIn');
                    //Clearing the email from local storage
                    this.localStorage.userEmail = ''
                    this.$router.push({name: 'login'});
                }
            }
        }
    }
</script>

<style scoped lang="scss">
    @import "../assets/scss/navbar";
</style>