                <span class="mdi mdi-chevron-left" v-show="!sideBarCollapsed"></span>
            </button>
        </div>
        <ul>
            <li @click="navigateTo('dashboard')">
                <span class="mdi mdi-palette mdi-18px"></span>
                <a href="javascript:void(0)">Dashboard</a>
            </li>
            <li @click="navigateTo('technicians')">
                <span class="mdi mdi-account-multiple mdi-18px"></span>
                <a href="javascript:void(0)">All Technicians</a>
            </li>
            <li @click="navigateTo('map')">
                <span class="mdi mdi-map mdi-18px"></span>
