<template>
    <div class="page-wrapper">
        <div class="frm-login">
            <div class="frm-header">
                <h1>Log in</h1>
                <small class="sub-title">TechniciansFinder web Portal</small>
            </div>
            <div class="frm-body">
                <t-input-field v-validate="'required|email'"
                               name="email"
                               :has-error="errors.first('email')"
                               label="User E-mail"
                               v-model="email"/>
                <t-input-field v-validate="'required'"
                               name="password"
                               :has-error="errors.first('password')"
                               label="Password"
                               password
                               v-model="password"
                               @keyup.native.enter="login"/>

                <div class="alert-error" v-show="operation.response.failed">
                    {{operation.response.message}}
                </div>
            </div>
            <div class="frm-footer">
                <a href="" @click.prevent="login">Login</a>
            </div>
        </div>
        <a href="" class="btn-link btn-forgot-pswd">Forgot password?</a>

        <loading :active.sync="operation.inProgress"
                 :can-cancel="false"
                 :is-full-page="true"
                 :background-color="operation.loader.backgroundColor"
                 :loader="operation.loader.type"
                 :color="operation.loader.color"/>
    </div>
</template>

<script>

    import TInputField from "../components/TInputField";
    import Loading from 'vue-loading-overlay';
    import 'vue-loading-overlay/dist/vue-loading.css';

    export default {
        components: {
            Loading,
            TInputField
        },
        name: "login",
        mounted() {
            if (window.$cookies.isKey('loggedIn')) {
                // this.$router.push({name: 'dashboard'})
            }
        },
        data() {
            return {
                email: '',
                password: '',
                operation: {
                    inProgress: false,
                    loader: {
                        backgroundColor: '#fff',
                        color: '#0294ff',
                        type: 'spinner' //Possible values: dots, spinner
                    },
                    response: {
                        failed: false,
                        message: ''
                    }
                }
            }
        },
        methods: {
            login() {
                let vm = this

                this.operation.response.failed = false

                this.$validator.validateAll().then(() => {
                    if (!this.errors.any()) {
                        vm.authenticate();
                    }
                })
            },
            authenticate() {
                let vm = this
                vm.operation.inProgress = true
                vm.$http.post(
                    vm.$fullUrl(vm.$store.state.api.endPoints.postLogin),
                    {
                        email: vm.email,
                        password: vm.password
                    }
                ).then(function ({data}) {
                    vm.$cookies.config('1d')
                    vm.$cookies.set('loggedIn', {loggedIn: true});
                    /*
                       Saving email so that it can be displayed in dashboard even when user refreshes the page
                     */
                    vm.localStorage.loginData = data

                    //Navigating to dashboard after successful login
                    vm.$router.push({name: 'dashboard'})

                }).catch((error) => {
                    vm.operation.response.failed = true
                    vm.operation.response.message = error.response.data.message
                }).finally(() => {
                    vm.operation.inProgress = false
                });
            }
        }
    }
</script>

<style scoped lang="scss">
    @import "../assets/scss/vars";

    .page-wrapper {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: 100vh;
        color: $primaryColor;

        background-color: $bg-color;
    }

    .frm-login {
        background-color: white;
        width: 360px;
        /*margin: auto;*/
        border: 1px solid rgba(0, 0, 0, 0.1);
        border-radius: 8px;
        box-shadow: 0 5px 5px -3px rgba(0, 0, 0, 0.2), 0 8px 10px 1px rgba(0, 0, 0, 0.14), 0 3px 14px 2px rgba(0, 0, 0, 0.12);

        .frm-header {
            border-top-left-radius: 8px;
            border-top-right-radius: 8px;
            display: flex;
            flex-direction: column;
            align-items: center;
        }
        .frm-body {
            padding: 36px 16px;
        }
        .frm-footer {
            background-color: $secondaryColor;
            color: white;
            padding: 16px;
            border-bottom-left-radius: 8px;
            border-bottom-right-radius: 8px;
            text-align: center;
            & > * {
                color: white;
            }
            a {
                text-decoration: none;
            }
        }
    }

    .btn-link {
        text-decoration: none;
    }

    .btn-forgot-pswd {
        margin: 8px 0;
        transition: all 1s;
        color: $primary;
        &:hover {
            text-decoration: underline;
        }
    }

    .text-center {
        text-align: center;
    }

    .alert-error {
        margin-bottom: -16px !important;
    }

</style>