<template>
    <div class="profile">
        <div class="page-label">Profile Settings</div>
        <div class="row">
            <div class="col-md-4">
                <div class="profile-image">
                    <img src="../assets/user.jpg" alt="">
                    <h3>{{localStorage.userEmail}}</h3>
                </div>
            </div>
            <div class="col-md-8">
                <div class="settings">
                    <h2>Change Password</h2>
                    <hr>
                    <div class="alert-message" :class="{'is-success': operation.response.success,'is-error':operation.response.failed}">
                        <div>
                            <span class="mdi"
                                  :class="{'mdi-check': operation.response.success,'mdi-alert':operation.response.failed}"></span>
                            {{operation.response.message}}
                        </div>
                    </div>
                    <form action="">
                        <!--<h4 class="s-title no-bar">Old Password</h4>-->
                        <t-input-field v-model="oldPassword"
                                       label="Enter old password"
                                       password
                                       v-validate="'required'"
                                       name="Old-Password"
                                       :has-error="errors.first('Old-Password')"/>
                        <!--<h4 class="s-title no-bar">Password</h4>-->
                        <t-input-field v-model="password"
                                       label="Enter new password"
                                       password
                                       v-validate="'required'"
                                       name="password"
                                       :has-error="errors.first('password')"/>
                        <!--<h4 class="s-title no-bar">Re-enter password</h4>-->
                        <t-input-field v-model="confirmPassword"
                                       label="Confirm new password"
                                       password
                                       v-validate="'required'"
                                       name="password_confirmation"
                                       :has-error="errors.first('password_confirmation')"/>

                        <button type="button" class="btn elevation-1 primary mg-t-20"
                                @click="">
                            Save <span class="mdi mdi-send"></span>
                        </button>
                    </form>
                    <loading :active.sync="operation.inProgress && !operation.isFetch"
                             :can-cancel="false"
                             :is-full-page="false"
                             :background-color="operation.loader.backgroundColor"
                             :loader="operation.loader.type"
                             :color="operation.loader.color"/>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import TInputField from "../components/TInputField";
    import Loading from 'vue-loading-overlay';
    import 'vue-loading-overlay/dist/vue-loading.css';

    export default {
        name: "profile",
        components: {
            TInputField,
            Loading
        },
        data() {
            return {
                oldPassword: '',
                password: '',
                confirmPassword: '',
                operation: {
                    inProgress: false,
                    isFetch: false,
                    loader: {
                        backgroundColor: '#fff',
                        color: '#001c46',
                        type: 'spinner' //Possible values: dots, spinner
                    },
                    response: {
                        failed: false,
                        success: false,
                        message: '',
                        password: 'password',
                    }
                },
            }
        },
        saveChanges() {

        }
    }
</script>

<style scoped lang="scss">

    .profile {
        background-color: white;
        /*display: flex;*/
        height: 100%;
        padding-top: 16px;
        padding-bottom: 16px;
    }

    .profile-image {
        padding: 8px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        border-radius: 4px;
        img {
            width: 104px;
            border-radius: 50%;
        }
    }

    .settings {
        position: relative;
        background-color: #fafafa;
        padding: 8px 16px 24px 16px;
        form {
            width: 60%;
            margin-left: auto;
            margin-right: auto;
            margin-top: 16px;
        }
    }

    .page-label {
        color: rgba(0, 0, 0, 0.5);
        padding: 8px;
        text-transform: capitalize;
        font-size: 1.2em;
    }

    .alert-message {
        padding: 8px 16px;
        margin: 0 8px;
        border-radius: 4px;
        text-align: center;
        &.is-error {
            background-color: rgba(255, 0, 0, 0.05);
            color: rgba(255, 0, 0, 0.8);
        }
        &.is-success {
            background-color: rgba(0, 255, 0, 0.03);
            color: rgba(0, 210, 0, 1);
        }
    }
</style>