<template>
    <div class="content">
        <div class="header">
            <h2 class="md-title">Work Suggestions To apply for</h2>
        </div>
        <div class="content-wrapper">
            <div class="row mg-y-20">
                <div class="col-md-8">
                    <div class="filter-box">
                        <!--<t-search-field v-model="filter.searchText"/>-->
                    </div>
                </div>
            </div>
            <div class="tbl-card">
                <md-table>
                    <md-table-row>
                        <md-table-head>Title</md-table-head>
                        <md-table-head>Work Category</md-table-head>
                        <md-table-head>Description</md-table-head>
                        <md-table-head>Status</md-table-head>
                        <md-table-head>Submitted at</md-table-head>
                        <md-table-head>Action</md-table-head>
                    </md-table-row>
                    <md-table-row v-for="(item,index) in workSuggestions">
                        <md-table-cell>{{ item.title }}</md-table-cell>
                        <md-table-cell>{{ item.category }}</md-table-cell>
                        <md-table-cell>{{ item.description }}</md-table-cell>
                        <md-table-cell>{{ item.status }}</md-table-cell>
                        <md-table-cell>{{ item.created_at }}</md-table-cell>
                        <md-table-cell>
                            <div class="tbl-row-action">
                                <a href="" class="btn-link btn-apply"
                                   @click.prevent="applyForWork(item,index)"
                                   :key="item.id">
                                    <span class="mdi mdi-send"></span> Apply
                                </a>
                            </div>
                        </md-table-cell>
                    </md-table-row>
                </md-table>
                <div class="empty-table" v-show="workSuggestions.length === 0">
                    <small>No data available</small>
                </div>
            </div>
        </div>
        <div class="loader">
            <loading :active.sync="operation.inProgress && operation.isFetch"
                     :can-cancel="false"
                     :is-full-page="false"
                     :background-color="operation.loader.backgroundColor"
                     :loader="operation.loader.type"
                     :color="operation.loader.color"
                     :height="32"/>
        </div>

        <!--<md-dialog :md-active.sync="dialogs.deletion">
            <div class="confirmation-dialog">
                <div class="header">
                    <span class="mdi mdi-information"></span>
                </div>
                <div class="dialog-body">
                    <h2 class="dialog-title">
                        The user will be deleted permanently!
                    </h2>
                    <h5 class="dialog-sub-title">
                        Are you sure to proceed?
                    </h5>
                </div>
                <div class="dialog-actions">
                    <button class="btn-action" @click="closeModal">Cancel</button>
                    <button class="btn-action danger" @click="executeDelete">Delete</button>
                </div>
                <loading :active.sync="operation.inProgress && !operation.isFetch"
                         :can-cancel="false"
                         :is-full-page="false"
                         :background-color="operation.loader.backgroundColor"
                         :loader="operation.loader.type"
                         :color="operation.loader.color"
                         :height="32"/>
            </div>
        </md-dialog>-->
    </div>
</template>

<script>
    import TInputField from "../components/TInputField";
    import TTextField from "../components/TTextField";
    import TSearchField from "../components/TSearchField";

    import Loading from 'vue-loading-overlay';
    import 'vue-loading-overlay/dist/vue-loading.css';

    export default {
        name: "work_suggestion",
        components: {
            TInputField,
            TTextField,
            TSearchField,
            Loading,
        },
        mounted() {
            let loggedInUserData = JSON.parse(localStorage.loginData)
            if (loggedInUserData.technician) {
                this.getWorkSuggestions(loggedInUserData.technician.id)
            }
        },
        data() {
            return {
                filter: {
                    key: 'name', //name,address
                    searchText: '',
                    searchTypes: ['name', 'address']
                },
                dialogs: {
                    deletion: false,
                    saveAndUpdate: false,
                },
                editedItem: {
                    id: 1,
                    user_id: 2,
                    work_category_id: 2,
                    title: "Repairing Laptop",
                    description: "I need a technician who can repair my laptop. The price is 15000 Rwf but negotiable.",
                    status: "pending",
                    image1: null,
                    image2: null,
                    image3: null,
                    created_at: "2019-07-04 19:59:43",
                },
                defaultItem: {
                    name: "",
                    email: "",
                    password: '',
                },
                editedIndex: null,
                operation: {
                    inProgress: true,
                    isFetch: false,
                    loader: {
                        backgroundColor: '#fff',
                        color: '#001c46',
                        type: 'dots' //Possible values: dots, spinner
                    },
                    response: {
                        failed: false,
                        message: '',
                        password: 'password',
                    }
                },
                workSuggestions: [],
            }
        },
        computed: {},
        methods: {
            openDialog(isUpdate) {
                this.isUpdate = isUpdate
                this.dialogs.saveAndUpdate = true
            },
            closeModal() {
                this.dialogs.deletion = false
                this.dialogs.saveAndUpdate = false
            },

            getWorkSuggestions(id) {
                let vm = this
                vm.startLoading(true)
                vm.$http.get(
                    vm.$fullUrl(vm.$store.state.api.endPoints.getWorkSuggestions + id),
                ).then(function ({data}) {
                    vm.workSuggestions = data
                }).catch(() => {
                    // vm.operation.response.message = "An error occurred. Try again"
                }).finally(() => {
                    vm.stopLoading()
                });
            },
            applyForWork(item, index) {
                let vm = this
                vm.startLoading(false)

                let workId = item.id
                let technicianId = JSON.parse(localStorage.loginData).technician.id

                vm.$http.post(
                    vm.$fullUrl(vm.$store.state.api.endPoints.postApplyForWork),
                    {
                        technician_id: technicianId,
                        work_id: workId
                    }
                ).then(function () {
                    vm.workSuggestions.splice(index, 1)
                }).catch(() => {
                    // vm.operation.response.message = "An error occurred. Try again"
                }).finally(() => {
                    vm.stopLoading()
                });
            },
            startLoading(isFetch = false) {
                this.operation.inProgress = true
                this.operation.isFetch = isFetch
                this.operation.response.failed = false
                if (isFetch) {
                    this.workSuggestions.length = 0
                }
            },
            stopLoading() {
                this.operation.inProgress = false
            },
        }
    }
</script>

<style scoped lang="scss">
    /*@import "../assets/scss/style";*/

    .btn-apply{
        color: blue !important;
        font-weight: bold;
    }
</style>