<template>
    <div class="content">
        <div class="header">
            <h2 class="md-title">All Technicians on Map</h2>
        </div>
        <div class="content-wrapper">
            <div class="search-places">
                <div class="input-box">
                    <gmap-autocomplete @place_changed="getNearbyPlaces" ref="searchPlace"/>
                    <span class="mdi mdi-magnify mdi-24px"></span>
                </div>
            </div>
            <div id="map">
                <gmap-map :options="map.mapOptions" :center="map.center" :zoom="map.mapZoomLevel"
                          style="width:100%;  height: 340px;">
                    <gmap-marker :position="map.center"
                                 :draggable="true"
                                 :animation="2"
                                 :label="{text:map.center.name,color:'#300000'}"
                                 :title="map.center.name"
                                 @dblclick="map.mapZoomLevel+=2"></gmap-marker>
                    <gmap-marker :key="index"
                                 v-for="(m, index) in map.technicians"
                                 :title="m.f_name +' '+ m.l_name"
                                 :position="{lat: m.work_place_lat,lng: m.work_place_long}"
                                 @click="setLocationCenter(m)"/>
                </gmap-map>
            </div>
            <loading :active.sync="operation.inProgress && operation.isFetch"
                     :can-cancel="false"
                     :is-full-page="true"
                     :background-color="operation.loader.backgroundColor"
                     :loader="operation.loader.type"
                     :color="operation.loader.color"/>
        </div>
    </div>
</template>

<script>
    import Loading from 'vue-loading-overlay';
    import 'vue-loading-overlay/dist/vue-loading.css';

    export default {
        name: "map",
        mounted() {
            this.geoLocate()
            this.getAllTechnicians()

            // this.$refs.searchPlace.addEventListener('focus', this.updateUi)
        },
        components: {
            Loading,
        },
        data() {
            return {
                map: {
                    center: {
                        lat: -1.9546051000000002,
                        lng: 30.072289599999998,
                        name: 'Your current location',
                        locationId: 0,
                    },
                    markers: [],
                    places: [],
                    currentPlace: null,
                    technicians: [],
                    mapOptions: {
                        zoomControl: true,
                        mapTypeControl: false,
                        scaleControl: false,
                        streetViewControl: false,
                        rotateControl: false,
                        fullscreenControl: true,
                        disableDefaultUi: false
                    },
                    mapZoomLevel: 15,
                    icon: {
                        url: require('../assets/markers/marker_5.png'),
                        scaledSize: {width: 50, height: 32},
                        labelOrigin: {x: 16, y: -10}
                    },
                },
                operation: {
                    inProgress: true,
                    isFetch: false,
                    loader: {
                        backgroundColor: '#fff',
                        color: '#001c46',
                        type: 'dots' //Possible values: dots, spinner
                    },
                    response: {
                        failed: false,
                        message: '',
                        password: 'password',
                    }
                },
            }
        },
        methods: {
            async getAllTechnicians() {
                let vm = this
                vm.startLoading(true)
                await vm.$http.get(vm.$fullUrl(vm.$store.state.api.endPoints.getTechnicians)).then(({data}) => {
                    vm.map.technicians = data
                }).catch((error) => {
                    alert('An error occurred. Try again')
                }).finally(() => {
                    vm.stopLoading()
                })
            },
            startLoading() {
                this.operation.inProgress = true
            },
            stopLoading() {
                this.operation.inProgress = false
            },
            setLocationCenter(location) {
                this.map.center.lat = location.work_place_lat
                this.map.center.lng = location.work_place_long
                this.map.center.name = location.f_name + ' ' + location.l_name
            },
            geoLocate: function () {
                navigator.geolocation.getCurrentPosition(position => {
                    this.center = {
                        lat: position.coords.latitude,
                        lng: position.coords.longitude
                    };
                });
            },
            getNearbyPlaces(place) {
                let vm = this
                vm.startLoading()

                let lat = place.geometry.location.lat()
                let lng = place.geometry.location.lng()

                console.log("Latitude", lat)
                console.log("Longitude", lng)

                vm.$http.post(
                    vm.$fullUrl(vm.$store.state.api.endPoints.postGetNearbyPlaces),
                    {
                        latitude: lat,
                        longitude: lng
                    }
                ).then(({data}) => {
                    vm.map.technicians = data
                    if (data.length === 0) {
                        alert('Sorry no technicians nearby that place within 10 km')
                    }
                }).catch((error) => {
                    alert('An error occurred. Try again')
                }).finally(() => {
                    vm.stopLoading()
                })
            },
            setPlace(place) {
                // this.currentPlace = place;

                /*this.currentPlace.geometry.location.lat()
                this.currentPlace.geometry.location.lng()*/

                // this.addMarker()
            },
            updateUi(e) {
                alert('Focused')
            }
        }
    }
</script>

<style lang="scss">

    #map {
        width: 100%;
        height: 100%;
    }

    .search-places {
        .input-box {
            border: 1px solid #ccc;
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
            padding: 4px 16px;
            input {
                padding: 4px 16px;
                margin-right: 4px;
                width: 100%;
                border: none;
                outline: none;
            }
        }
    }
</style>